@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap);

/* =========== google fuentes de google =========== */

/* ======== estilos base ======= */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  background: #0a071a;
}

section,
footer {
  padding: 70px 0px;
}

h1,
h2 {
  font-size: 2rem;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  color: #f5b70a;
}

p {
  font-size: 1rem;
  color: #c4c4c4;
  line-height: 30px;
  font-family: "Rubik", sans-serif;
}

a {
  text-decoration: none;
  color: unset;
}

i {
  color: #fff;
}

.header{
    width: 100%;
    height: 80px;
    line-height: 80px;
    position: fixed;
    top: 0;
    left: 0;
z-index: 9;
background: #0a071a;
}

.navigation{
display: flex;
align-items: center;
justify-content: space-between;
}
.logo h2{
    margin-bottom: 0;
}

.nav__list{
    display: flex;
    align-items: center;
    margin-bottom: center;
    list-style: none;
    grid-column-gap: 2.7rem;
    -webkit-column-gap: 2.7rem;
            column-gap: 2.7rem;
}

nav__item, a{
    color: #fff;
    cursor:pointer;
transition: 0.3s;
}

.nav-item,  a:hover{
    color:#F5B70A;
}
.custom__search{
    display: flex;
    align-items: center;
    padding: 10px 25px;
    height: 35px;
    border-radius:  50px; 
    background: #141124;
}

.custom__search input {
    border: none;
    outline: none;
    background: transparent;
    color: #fff;

}

.custom__search span i{
    cursor: pointer;
}

.mobile__menu span i {
font-size: 1.3rem;
}

.mobile__menu{
    display: none;
}

.cart__icon{
    position: relative;
    z-index: 999;
    width:40px;
    height:30px;
    border-radius:5px;
    background: #f76e12;
    display: flex;
    align-items:center;
    justify-content: center;
}

.badge{
    position: -2px;
    right: -3px;
    z-index: 1000;
    color: #141124;
    font-size: .8rem;
    font-weight: 700;
}

/* area responsiva*/


@media  only screen and (max-width: 768px) {
    .nav__menu{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 99;
        background: #0a071a7e;
        display: none;
    }
    .nav__list__wrapper{
        position:absolute;
        top: 0;
        right: 0;
        width: 250px;
        height: 100%;
        z-index: 100;
        background: #141124;
        flex-direction: column;
        box-shadow: 5px 5px 15px -5px #F5B70A;
        justify-content: center !important;
        padding: 0px 2px ;
        
    }
.nav__list{
    flex-direction: column;
}
.nav__item a {
    font-size: .9rem;

}
.header{
    line-height: 40px;
    padding: 10px 0px;
}

.custom__search{
    border: 1px solid #c4c4c425;
    padding: 5px 15px;
    font-size:  0.9rem; 
    height: 30px;
    margin-top: 15px;
}
.mobile__menu{
    display: block;
}



.logo h2{
    font-size: 1.5rem;

}


.active__menu{
    display: block;
}
}
.btn{
padding: 7px 25px;
border-radius: 5px;
background: #f76e11;
color:#fff;
font-size: 0.9rem;
margin-top: 30px;

}

.btn:hover{
    color:#f76e11;
}
.single__product{
    background: #141124;
    border-radius: 10px;
    cursor:pointer;

}

.single__product:hover{
    box-shadow: 5px 5px 10px 5px #f5b70a;
    
}


.product__content{
    padding: 0px 20px;
    padding-bottom: 15px ;
}

.product__content h6{
    color:#f5b70a;
    text-align: center;

}


.rating span i {
    color:#f76e11;

}
.rating{
    margin-bottom: 10px;
}

.price{
    color:#fff;

}

.price span{
color:#f5b70a;
font-size: 1.3rem;
font-weight: 500;
}

.shopping__icon{
    width: 30px;
    height: 30px;
    border-radius:5px;
    background: #3b11f7;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.single__feature span {
    width: 50px;
    height: 50px;
    border-radius: 50%;
background: #ff766b;
display: flex;
align-items: center;
justify-content: center;
margin-bottom: 10px;

}

.single__feature span i{
    font-size: 1.5rem;
}
.choose__content h4{
    color: #fff;
    margin-bottom: 15px;


}

.single__feature h6{
    color: #f5b70a;
    margin-bottom: 0;


}

.single__feature p{
    font-size: 0.7rem;
}
.feature__icon-2{
    color: #f5b70a;
}
.filter-btn{
    border:1px solid #f76e11;
    padding: 7px 25px ;
    background: transparent;
    margin-left: 15px;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
}

.menu__title{
    color:#f5b70a;
}

.active__btn {
    background:#f76e11;
}
